import { Component, OnInit } from "@angular/core";
import { ModalController } from "@ionic/angular";
import { Geolocation } from "@ionic-native/geolocation/ngx";

import {
  GoogleMaps,
  GoogleMap,
  GoogleMapsEvent,
  GoogleMapOptions,
  CameraPosition,
  MarkerOptions,
  Marker,
  Environment,
  Circle,
  LatLng,
} from "@ionic-native/google-maps/ngx";

@Component({
  selector: "app-set-location",
  templateUrl: "./set-location.page.html",
  styleUrls: ["./set-location.page.scss"],
})
export class SetLocationPage {
  map: GoogleMap;
  constructor(
    private modalController: ModalController,
    private geolocation: Geolocation
  ) {}

  // ngOnInit() {
  //   this.loadMap();
  // }

  ionViewLoaded = false;
  ionViewDidEnter() {
    if (!this.ionViewLoaded) {
      this.ionViewLoaded = true;
      this.ionViewDidLoad();
    }
  }

  ionViewDidLoad() {
    this.loadMap();
  }

  loadMap() {
    // console.log(data);

    // let initialPos = { lat: data.coords.latitude, lng: data.coords.longitude };

    this.geolocation
      .getCurrentPosition()
      .then((resp) => {
        // resp.coords.latitude
        // resp.coords.longitude
        let initialPos = {
          lat: resp.coords.latitude,
          lng: resp.coords.longitude,
        };
        console.log(initialPos);
        let mapOptions: GoogleMapOptions = {
          camera: {
            target: initialPos,
            zoom: 14,
            tilt: 30,
          },
        };

        this.map = GoogleMaps.create("map_canvas", mapOptions);
      })
      .catch((error) => {
        console.log("Error getting location", error);
      });

    //  let watch = this.geolocation.watchPosition();
    //  watch.subscribe((data) => {
    //   // data can be a set of coordinates, or an error (if an error occurred).
    //   // data.coords.latitude
    //   // data.coords.longitude
    //  });
  }

  close() {
    // this._location.back();
    this.modalController.dismiss({ latitude: "", longitude: "" });
  }

  
  
}
